/**
 * Default with selector.  You can't do an @import statement in
 * a conditional/if block, so hence the need for another "version"
 */

$selector-wrapper: '.strib' !default;

@import 'variables';
@import 'mixins/_all';
@import 'content/fonts';

@import 'resets/reset';
@import 'resets/normalize';

#{$selector-wrapper} {
  @import 'common-default';
}
